<template>
  <lay-header
    :showBack="true"
    :tBarColor="'#FFFFFF'"
    :isGetingItems="isGetingItems"
  >
    <v-layout column class="lay-body">
      <span class="txt-top"
        >비밀번호를 <br />다시 한번 입력해 주세요.</span
      >
      <c-input
        :hint="'영문+숫자 6자리 이상'"
        :value.sync="inputList.pw.value"
        class="mt-5"
        :type="'password'"
        :errorMsg="errorCnt > 0 ? '비밀번호가 일치하지 않습니다.' : ''"
      />
      <v-spacer />
      <c-btn
        :height="48"
        :thema="`default-bg${!validatePassword(inputList.pw.value) ? '-disable' : ''}`"
        :fontWeight="400"
        :fontSize="16"
        :readOnly="!validatePassword(inputList.pw.value)"
        :onClick="onClickActionNext"
        class="mt-5"
        >다음</c-btn
      >
    </v-layout>
  </lay-header>
</template>

<script>
export default {
  middleware: "guest",
  props: ['pass', 'loginId', 'userId'],
  data() {
    return {
      isGetingItems: false,
      inputList: {
        pw: {
          value: null,
          validation: () => {
            return this.checkPw();
          },
        },
      },
      errorCnt: 0,
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    if(!this.pass || !this.loginId) {
      this.showToast("잘못된 경로입니다.")
      this.$router.go(-1)
      return;
    }
  },
  destroyed() {},
  methods: {
    async onClickActionNext() {
      for (let key in this.inputList) {
        if (this.inputList[key].validation) {
          if (!this.inputList[key].validation()) {
            this.errorCnt++
            if(this.errorCnt > 4) {
              this.showPopup({
                title: "입력 비밀번호 5회 오류",
                okTxt: "처음부터 다시 실행",
                msg: "입력한 비밀번호가 계속 일치하지 않습니다.\n비밀번호를 처음부터 다시 설정해주세요.",
                okAction: () => {
                  this.$router.go(-1)
                },
              })
            }
            return false;
          }
        }
      }
      this.errorCnt = 0
      if (this.isGetingItems) {
        return;
      }
      this.isGetingItems = true;
      this.api({
        url: "/login/join",
        method: "post",
        data: {
          userId: this.userId,
          loginId: this.loginId,
          password: this.inputList.pw.value,
          passwordCnf: this.pass
        }
      }).then(() => {
        this.isGetingItems = false;
        this.pushRouter({ name: "complete" });
      }).catch(() => {
        this.isGetingItems = false;
      })

    },
    checkPw() {
      if (this.inputList.pw.value != this.pass) {
        return false;
      }
      return true;
    },
  },
};
</script>

